/* Footer / Copyright Information -------------------------------------------------------*/
footer#footer {
  #footer-copyright {
    display: block;
    font-size: .9em;
    padding-bottom: 15px;
    text-align: center;
    #footer-left, #footer-middle, #footer-right {
      display: inline-block;
    }
    #footer-left, #footer-right {
      width:20%;
    }
    #footer-right {
      text-align: left;
      ul {
        list-style-type: none;
      }
    }
    #footer-middle {
      border-left: solid 1px #FFF;
      border-right: solid 1px #FFF;
      width:50%;
      padding: 5px 15px;
    }
  }
}
#footer-copyright p {
  margin: 0 0 15px 0;
  color: white!important;
}

#footer-copyright a {
  display: inline-block;
  color: white;
  margin-bottom: 3px;
  border-bottom: solid 1px #FFF;
}
#footer-copyright a:hover {
  color: #fed463;
  border-bottom-color: #fed463;
}

