/* Top - Lead Story ****************************************/
body.front .top-story {
  display: block;
  width:100%;
  height:250px;
  margin: 0;
  border:solid 1px #CAC7C7;
  #top-image {
    display: block;
    //float:left;
    float:right;
  }
  #lead-spot {
    display: block;
    //width:250px;
    max-width:440px;
    position: absolute;
    //right: 0;
    height: 230px;
    padding: 10px;
    //background-color: rgba(255,255,255,0.85);
    background-color: #FFFFFF;
    .lead-title h3 {
      margin:15px 0 5px;
      padding:0;
    }
    .lead-teaser {
      margin: 10px 25px 25px 50px;
    }
    .lead-more, .more-content {
      display: inline-block;
      position: absolute;
      bottom:10px;
    }
    .more-content {
      right: 10px;
    }
  }
}
.lead-date {
  border:solid 1px #CAC7C7;
  width:max-content;
  float:left;
  margin-right:10px;
  .month {
    display: block;
    padding:5px 10px;
    background-color: #215B83;
    color:#FFFFFF;
  }
  .day {
    display: block;
    padding:5px 10px;
    text-align:center;
  }
}

/* Standard top menu search -------------------------------------------------------*/
#navigation {
  position: relative;
  #block-search-form {
    position: absolute;
    right:15px;
    top:9px;
    z-index: 1000;
    .form-item-search-block-form input {
      height:25px;
      background-color: #FFFFFF;
    }
    #search-block-form {
      .icon {
        display:none;
        top:0;
        margin-left:8px;
        margin-top:6px;
        i.better_search {
          width:2em;
          height:2em;
        }
      }
      .fa.fa-search {
        font-size: 1.5em;
        margin-top: 3px;
        margin-left: 8px;
        color:#FFFFFF;
        &:hover {
          color:lightseagreen;
        }
      }
    }
  }
}



