  margin-bottom: 10px;
}

/* Front Page 4 Box Grid -------------------------------------------------------*/
body.front {
  #block-views-home-page-blocks-zen-block-9 {
    clear:both;
  }
  .front-four .view-content,
  .region-comm-news {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    .view-home-page-blocks-zen {
      width:100%;
    }
  }
  #block-views-home-page-blocks-zen-block-1,
  #block-views-home-page-blocks-zen-block-3,
  #block-views-home-page-blocks-zen-block-4,
  #block-views-home-page-blocks-zen-block-8,
  .front-four .views-row-odd,
  .front-four .views-row-even {
    width:24.7%;
    position: relative;
    #follow-story {
      position: relative;
      border: solid 1px #CAC7C7;
      padding: 10px;
      min-height: 440px;
      margin-bottom: 12px;
      .follow-image {
        margin-bottom:12px;
        display: block;
      }
      .lead-date {
        float:left;
        margin-right:10px;
      }
      h3, h5 {
        padding:0;
        margin:0;
      }
      h3 {
        min-height:39px;
      }
      .more-content {
        position:absolute;
        bottom:10px;
      }
    }
  }
  .fp-comm-news-title h2 {
    background-color: #26669E;
    color: #FFFFFF;
    text-align: center;
    padding:2px 0 5px;
    margin:0 0 20px;
  }
  .promoted:last-of-type {
    margin-right:0;
  }
}

